html{
    font-size: 16px;
}
body {
	font-family: "ProximaNova";
	background-color: $bg-color;
	color: $color;
    font-size:16px;
}
.fw-300 {
	font-weight: 300;
}
.fw-400 {
	font-weight: 400;
}
.fw-600 {
	font-weight: 600;
}
.fw-700 {
	font-weight: 700;
}
.btn{
    font-size: 15px;
    border-radius:90px;
    background-color:$primary-color;
    color:$color;
    border-color:$primary-color;
    padding: 12px 27px;
    &:hover{
        background-color:transparent;
        color:$color;
        border:1px solid $primary-color;
    }
    &.btn-s{
        padding: 12px 18px;

    }
    &.btn-l{
        font-size: 16px;
        padding: 19px 21px 19px 33px;
        img{
            margin-left: 48px;
        }

    }
    &.btn-secondary{
        background-color:transparent;
        border:1px solid $primary-color;
        &:hover{
            background-color:$primary-color;
        }
    }
}
.navbar-nav.w-100.justify-content-between.mx-auto{
    max-width: 650px;;
}
.navbar-nav.w-100.justify-content-between.mx-auto .nav-link{
    padding: 10px 8px;
}